import {
  AppBar,
  Button,
  FormControl,
  IconButton,
  InputLabel,
  makeStyles,
  TextField,
  Toolbar,
  Typography,
} from "@material-ui/core";
import Modal from "@material-ui/core/Modal";
import Select from "@material-ui/core/Select";
import { useEffect, useState } from "react";
import useStateRef from "react-usestateref";
import Fade from "@material-ui/core/Fade";
import Backdrop from "@material-ui/core/Backdrop";
import AddIcon from "@material-ui/icons/Add";
import SubIcon from "@material-ui/icons/Remove";
import RemoveIcon from "@material-ui/icons/Delete";
import { v4 as uuidv4 } from "uuid";
import { useNavigate } from "react-router-dom";
import Helper from "./Helpers";
import Swal from "sweetalert2";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  tabTit:{
    fontWeight: "bold",
              width: "25%",
              color: "white",
              paddingLeft:'1%',
              paddingRight:'1%'
  },
  tabSty:{
    display: "flex", width: "80%" ,
    [theme.breakpoints.down('md')]:{
      width: "90vw",
    },
    [theme.breakpoints.down('sm')]:{
      width: "100vw",
    },
  },
  tabSubSty:{
    height: "40vh",
    backgroundColor: "#d2e0d2",
    overflow: "auto",
    width: "80%",
    [theme.breakpoints.down('md')]:{
      width: "90vw",
    },
    [theme.breakpoints.down('sm')]:{
      width: "100vw",
    },
  }
}));

export default function HomePage() {
  const classes = useStyles();
  const navigate = useNavigate();
  const [myItem, setMyItem] = useState([]);

  const [state, setState, stateRef] = useStateRef(0);
  const [displayAmount, setdisplayAmount, displayRef] = useStateRef(0);

  const [options, setOptions] = useState([]);

  const [receivedAmount, setReceivedAmount, receivedRef] = useStateRef();
  const [gst, setGst, gstRef] = useStateRef();
  const [grandTotal, setGrandTotal, grandRef] = useStateRef(0);
  const [changeAmount, setChangeAmount, changeRef] = useStateRef();
  const [balanceText, setBalanceText, balanceRef] = useStateRef("Balance");

  const handleReceivedAmount = (e) => {
    var text = e.target.value;
    setReceivedAmount(text.trim());

    if (receivedRef.current === isNaN(parseFloat(receivedRef.current))) {
      setReceivedAmount("");
    }

    var chn = parseFloat(grandRef.current) - parseFloat(receivedRef.current);

    if (parseFloat(chn) <= parseInt(0)) {
      setChangeAmount(parseFloat(chn) * -1);
      setBalanceText("Balance");
    } else {
      setChangeAmount(chn);
      setBalanceText("Due");
    }
    console.log(chn);
  };

  const handleGST = (e) => {
    var text = e.target.value;
    setGst(text.trim());

    if (gstRef.current === isNaN(parseFloat(grandRef.current))) {
      setGst("");
    }

    if (gstRef.current !== 0) {
      var extra =
        parseInt(displayRef.current) * (parseFloat(gstRef.current) / 100);

      var grand = parseFloat(extra) + displayRef.current;
      setGrandTotal(grand);
      console.log(grand);

      return;
    }
  };

  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    if (options.length === 0) {
      Swal.fire({
        position: "top-end",
        icon: "warning",
        title: "Please add the item first. ",
        showConfirmButton: false,
        timer: 2000,
      });
    } else {
      setOpen(true);
    }
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpt = (event) => {
    var va = event.target.value;
    var name = event.target.selectedOptions[0].text;
    let isExists = myItem.find((el) => el.name === name);
    console.log(isExists);
    if (isExists === undefined) {
      let foundOpt = options.find((el) => el.iName === name);

      if (foundOpt.iName !== "None") {
        const removeOpt = options.filter((item) => item.iName !== name);
        setOptions(removeOpt);
      }
    }

    if (name === "None") {
      alert("Please select item");
    } else {
      myItem.push({
        id: uuidv4(),
        name: name,
        amount: va,
        count: 1,
        originalAmount: va,
        isSelect: "",
      });
      setMyItem(myItem);
      setdisplayAmount(parseInt(displayRef.current) + parseInt(va)); //
      setGrandTotal(displayRef.current);
    }
  };

  const handleInc = (item, index) => {
    const temp = myItem[index];

    let inc = parseInt(temp["count"]) + 1;
    let amo = parseInt(temp["originalAmount"]);
    var newAr = [...myItem];

    newAr[index] = { ...newAr[index], count: inc, amount: amo * inc };

    setMyItem(newAr);

    var preGr = displayRef.current + "\t" + displayAmount;
    console.log(preGr);
  };

  const handleDec = (item, index) => {
    const temp = myItem[index];
    let inc = parseInt(temp["count"]);
    let amo = parseInt(temp["originalAmount"]);
    const dec = temp["amount"] - amo;
    temp["count"] = inc - 1;
    var newAr = [...myItem];
    newAr[index] = { ...newAr[index], count: inc - 1, amount: dec };
    setMyItem(newAr);
    setGrandTotal(displayAmount);

    if (temp["count"] === 0) {
      handleRemove(index);
    }
  };

  const handleRemove = (index) => {
    const newData = myItem.filter((item) => item.id !== myItem[index]["id"]);
    setMyItem(newData);
  };

  const removeItem = (index) => {
    handleRemove(index);
  };

  const handleAddShopItems = () => {
    navigate("/shop-item");
  };

  useEffect(() => {
    if (myItem.length !== 0) {
      var tol = 0;
      myItem.map((data) => {
        tol = parseInt(data.amount) + tol;
      });
      setMyItem(myItem);
      setdisplayAmount(tol);
    } else {
      setdisplayAmount(0);
      setChangeAmount(0);
      setGst("");
      setGrandTotal(0);
      setReceivedAmount("");

      var locList = localStorage.getItem("myList");
      if (locList === null) {
        setOptions([]);
      } else {
        var myData = JSON.parse(locList);
        console.log(myData);
        myData.splice(0, 0, { iName: "None", iPrice: "0" });
        setOptions(myData);
      }
    }
  }, [myItem]);

  // useEffect(() => {}, [displayAmount]);
  // useEffect(() => {}, [gst]);
  // useEffect(() => {}, [grandTotal]);
  // useEffect(() => {}, [receivedAmount]);
  // useEffect(() => {}, [changeAmount]);

  useEffect(() => {
    var locList = localStorage.getItem("myList");
    if (locList === null) {
      setOptions([]);
    } else {
      var myData = JSON.parse(locList);
      console.log(myData);
      myData.splice(0, 0, { iName: "None", iPrice: "0" });
      setOptions(myData);
    }
  }, []);

  return (
    <div>
      <AppBar position="static">
        <Toolbar style={{ backgroundColor: Helper().primaryColor }}>
          <div style={{ width: "100%" }}>
            <Typography
              style={{
                fontWeight: "bold",
                fontSize: "20px",
              }}
            >
              My Shop
            </Typography>
          </div>

          <div style={{ display: "flex", flexDirection: "row-reverse" }}>
            <Button
              variant="contained"
              color="primary"
              style={{
                margin: "1%",
                width: "150px",
                backgroundColor: Helper().secondaryColor,
              }}
              onClick={handleOpen}
            >
              Add Item
            </Button>

            <Button
              variant="contained"
              color="primary"
              style={{
                margin: "1%",
                width: "250px",
                backgroundColor: Helper().secondaryColor,
              }}
              onClick={handleAddShopItems}
            >
              Shop Items
            </Button>
          </div>
        </Toolbar>
      </AppBar>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <FormControl variant="outlined" className={classes.formControl}>
              <InputLabel htmlFor="outlined-price-native-simple">
                Category
              </InputLabel>
              <Select
                native
                value={state.name}
                onChange={handleOpt}
                label="Category"
                inputProps={{
                  name: "price",
                  id: "outlined-price-native-simple",
                }}
                defaultValue={options[0]}
              >
                {options.map((opt) => (
                  <option
                    value={opt.iPrice}
                    key={opt.iPrice}
                    style={{ display: opt.isSelect }}
                  >
                    {opt.iName}
                  </option>
                ))}
              </Select>
            </FormControl>
          </div>
        </Fade>
      </Modal>

      <div
        style={{
          display: "flex",
          width: "100%",
          alignItems: "center",
          flexDirection: "column",
          marginTop: "5vh",
        }}
      >
        <div  className={classes.tabSty}>
          <Typography
          className={classes.tabTit}
            style={{
              backgroundColor: Helper().secondaryColor,
            }}
          >
            Name
          </Typography>
          <Typography
          className={classes.tabTit}
            style={{
              backgroundColor: Helper().secondaryColor,
            }}
          >
            Quantity
          </Typography>
          <Typography
          className={classes.tabTit}
            style={{
              backgroundColor: Helper().secondaryColor,
            }}
          >
            Amount
          </Typography>
          <Typography
          className={classes.tabTit}
            style={{
              backgroundColor: Helper().secondaryColor,
            }}
          >
            Total
          </Typography>
          <Typography
          className={classes.tabTit}
            style={{
              backgroundColor: Helper().secondaryColor,
            }}
          >
            Remove
          </Typography>
        </div>

        <div
        className={classes.tabSubSty}
        >
          {myItem.map((item, index) => (
            <div style={{ display: "flex", flexDirection: "column" }}>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-around",
                  border: "1px solid black",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    width: "25%",
                    backgroundColor: "#A6CF98",
                  }}
                >
                  <Typography> {item.name} </Typography>
                </div>
                <div
                  style={{
                    display: "flex",
                    width: "25%",
                    backgroundColor: "#A6CF98",
                    alignItems: "center",
                  }}
                >
                  <IconButton onClick={() => handleDec(item, index)}>
                    <SubIcon style={{ color: "red" }} />
                  </IconButton>
                  <Typography variant="h6"> {item.count} </Typography>
                  <IconButton onClick={() => handleInc(item, index)}>
                    <AddIcon style={{ color: "green" }} />
                  </IconButton>
                </div>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    width: "25%",
                    backgroundColor: "#A6CF98",
                  }}
                >
                  <Typography> {item.originalAmount} </Typography>
                </div>

                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    width: "25%",
                    backgroundColor: "#A6CF98",
                  }}
                >
                  <Typography> {item.amount} </Typography>
                </div>

                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    width: "25%",
                    backgroundColor: "#A6CF98",
                  }}
                >
                  <IconButton onClick={() => removeItem(index)}>
                    <RemoveIcon style={{ color: "red" }} />
                  </IconButton>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      <div
        style={{
          display: "flex",
          flexDirection: "column",
          marginRight: "10vw",
        }}
      >
        <div
          style={{
            display: "flex",
            padding: "5px",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Typography
            variant="body1"
            style={{ textAlign: "end", fontWeight: "bold" }}
          >
            Total Amount :
          </Typography>
          <Typography
            style={{
              width: "150px",
              marginLeft: "50px",
              border: "1px solid black",
              padding: "5px",
            }}
            placeholder="1000"
          >
             {displayRef.current}
          </Typography>
        </div>

        <div
          style={{
            display: "flex",
            padding: "5px",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Typography
            variant="body1"
            style={{ textAlign: "end", padding: "5px", fontWeight: "bold" }}
          >
            GST :
          </Typography>
          <TextField
            value={gstRef.current}
            onChange={(e) => handleGST(e)}
            style={{ width: "150px", marginLeft: "50px" }}
            placeholder="0%"
          />
        </div>

        <div
          style={{
            display: "flex",
            padding: "5px",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Typography
            variant="body1"
            style={{ textAlign: "end", fontWeight: "bold" }}
          >
            Grand Total :
          </Typography>
          <Typography
            style={{
              width: "150px",
              marginLeft: "50px",
              border: "1px solid black",
              padding: "5px",
            }}
          >
             {grandRef.current}
          </Typography>
        </div>

        <div
          style={{
            display: "flex",
            padding: "5px",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Typography
            variant="body1"
            style={{ textAlign: "end", padding: "5px", fontWeight: "bold" }}
          >
            Received Amount :
          </Typography>
          <TextField
            value={receivedRef.current}
            onChange={(e) => handleReceivedAmount(e)}
            style={{ width: "150px", marginLeft: "50px" }}
            placeholder="In Rupee"
          />
        </div>

        <div
          style={{
            display: "flex",
            padding: "5px",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        >
          <Typography
            variant="body1"
            style={{ textAlign: "end", padding: "5px", fontWeight: "bold" }}
          >
            {balanceRef.current} :
          </Typography>
          <Typography
            style={{
              width: "150px",
              marginLeft: "50px",
              border: "1px solid black",
              padding: "5px",
              height: "25px",
            }}
          >
             {changeRef.current}
          </Typography>
        </div>
      </div>


    
      <div style={{display:'flex', placeContent:'center' , position:'fixed' , bottom:5, left:'0px' , right:'0px'}}>
      <p> Developed By  <span onClick={() => { window.open("https://mbganesh.github.io/my_profile/") }} style={{color:Helper().primaryColor , fontWeight:'bold' , cursor:'pointer'}}> @mbganesh </span> </p>
      </div>


    </div>
  );
}
