import {
  AppBar,
  Fab,
  Toolbar,
  Typography,
  makeStyles,
  TextField,
  Button,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import React from "react";
import Modal from "@material-ui/core/Modal";
import Fade from "@material-ui/core/Fade";
import Backdrop from "@material-ui/core/Backdrop";
import { useEffect, useState } from "react";
import useStateRef from "react-usestateref";
import { useNavigate } from "react-router-dom";
import Helper from "./Helpers";
import Swal from "sweetalert2";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    padding: "10px",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #064635",
    width: "25vw",
    [theme.breakpoints.down('md')]:{
      width: "50vw",
    },
    [theme.breakpoints.down('sm')]:{
      width: "80vw",
    },
    boxShadow: theme.shadows[5],
    // padding: theme.spacing(2, 4, 3),
  },
  container: {
    display: "flex",
    justifyContent: "space-between",
    color: "white",
    width: "30vw",
    [theme.breakpoints.down('md')]:{
      width: "80vw",
    },
    [theme.breakpoints.down('sm')]:{
      width: "100vw",
    },
  },
  container_sub:{
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
    height: "75vh",
    overflow: "auto",
    width: "30vw",
    [theme.breakpoints.down('md')]:{
      width: "80vw",
    },
    [theme.breakpoints.down('sm')]:{
      width: "100vw",
    },
   
  },
  fabStyle:{
    position: "fixed",
    bottom: 20,
    right: 25,
    color: "white",
  }
}));

export default function AddShopItems() {
  const classes = useStyles();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);

  const [singleItem, setSingleItem] = useState({
    iName: "",
    iPrice: "",
  });

  const [itemList, setItemList, itemListRef] = useStateRef([
    {
      iName: "None",
      iPrice: "0",
    },
  ]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleAddItem = (e) => {
    if (singleItem.iName === "" || singleItem.iPrice === "") {
      Swal.fire({
        position: "top-end",
        icon: "warning",
        title: "Field Cannot be empty",
        showConfirmButton: false,
        timer: 3000,
      });
    } else {
      let foundData = itemList.find((el) => el.iName === singleItem.iName);

      if (foundData === undefined) {
        setItemList([...itemList, singleItem]);
        setOpen(false);

        setSingleItem({
          iName: "",
          iPrice: "",
        });

        var json = JSON.stringify(itemListRef.current);

        console.log(json);

        localStorage.setItem("myList", json);
      } else {
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Already Item exists",
          showConfirmButton: false,
          timer: 3000,
        });

        setSingleItem({
          iName: "",
          iPrice: "",
        });
      }
    }

    // if(itemList.length === 0 ){
    //     setItemList([...itemList , {
    //         iName: "None",
    //         iPrice: "0",
    //       }])
    //       return;
    // }
  };

  const handleName = (e) => {
    var txt = e.target.value;
    setSingleItem({ ...singleItem, iName: txt });
  };

  const handlePrice = (e) => {
    var txt = e.target.value;
    setSingleItem({ ...singleItem, iPrice: txt });
  };

  useEffect(() => {
    var locList = localStorage.getItem("myList");

    if (locList === null) {
      setItemList([]);
    } else {
      setItemList(JSON.parse(locList));
    }
  }, []);

  return (
    <div>
      <AppBar position="static">
        <Toolbar style={{ backgroundColor: Helper().primaryColor }}>
          <div style={{ width: "100%" }}>
            <Typography
              style={{
                fontWeight: "bold",
                fontSize: "20px",
              }}
            >
              Shop Items
            </Typography>
          </div>

          <div style={{ display: "flex", flexDirection: "row-reverse" }}>
            <Button
              variant="contained"
              color="primary"
              style={{
                margin: "1%",
                width: "150px",
                backgroundColor: Helper().secondaryColor,
              }}
              onClick={() => {
                navigate("/");
              }}
            >
              Home
            </Button>

            <Button
              variant="contained"
              color="primary"
              style={{
                margin: "1%",
                width: "150px",
                backgroundColor: "red",
              }}
              onClick={() => {
                if (itemList.length === 0) {
                  Swal.fire("No Item Found.");
                } else {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: Helper().primaryColor,
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      Swal.fire(
                        "Deleted!",
                        "Your Item has been deleted.",
                        "success"
                      ).then(() => {
                        localStorage.clear();
                        window.location.reload();
                      });
                    }
                  });
                }
              }}
            >
              Clear All
            </Button>
          </div>
        </Toolbar>
      </AppBar>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Typography
              style={{
                backgroundColor: Helper().primaryColor,
                textAlign: "center",
                fontWeight: "bold",
                fontSize: "18px",
                color: "white",
                padding: "15px",
              }}
            >
              My Shop
            </Typography>
            <form className={classes.root} noValidate autoComplete="off">
              <div style={{ display: "flex", flexDirection: "column" }}>
                <TextField
                  style={{ margin: "10px" }}
                  required
                  onInput={(e) =>
                    (e.target.value = e.target.value.slice(0, 15))
                  }
                  id="outlined-required"
                  label="Item Name"
                  value={singleItem.iName}
                  onChange={(e) => handleName(e)}
                  variant="outlined"
                />
                <TextField
                  style={{ margin: "10px" }}
                  required
                  id="outlined-required"
                  type={"number"}
                  onInput={(e) => (e.target.value = e.target.value.slice(0, 5))}
                  label="Item Pirce"
                  value={singleItem.iPrice}
                  onChange={(e) => handlePrice(e)}
                  variant="outlined"
                />
                <Button
                  variant="contained"
                  style={{
                    margin: "10px",
                    color: "white",
                    backgroundColor: Helper().primaryColor,
                  }}
                  onClick={(e) => handleAddItem(e)}
                >
                  Add Item
                </Button>
              </div>
            </form>
          </div>
        </Fade>
      </Modal>

      <div
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          width: "100vw",
          marginTop: "25px",
        }}
      >
        <div
          className={classes.container}
          style={{
            backgroundColor: Helper().primaryColor,
          }}
        >
          <Typography
            style={{ padding: "20px", fontWeight: "bold", fontSize: "20px" }}
          >
            Name
          </Typography>
          <Typography
            style={{ padding: "20px", fontWeight: "bold", fontSize: "20px" }}
          >
            Price
          </Typography>
          
        </div>

        <div
        className={classes.container_sub}
          style={{
          
            backgroundColor: Helper().secondaryColor,
          }}
        >
          {itemList.map((item, index) => (
            <div
            className={classes.container}
              style={{
                backgroundColor: Helper().secondaryColor,
              }}
            >
              <Typography
                style={{
                  padding: "20px",
                  fontSize: "18px",
                }}
              >
                {item.iName}
              </Typography>
              <Typography
                style={{
                  padding: "20px",
                  fontSize: "18px",
                }}
              >
                {item.iPrice}
              </Typography>

            </div>
          ))}
        </div>
      </div>

      <Fab
  className={classes.fabStyle}
        style={{
          backgroundColor: Helper().primaryColor,
        }}
        onClick={handleOpen}
      >
        <AddIcon />
      </Fab>

      <div style={{display:'flex', placeContent:'center' , position:'fixed' , bottom:5, left:'0px' , right:'0px'}}>
      <p> Developed By  <span onClick={() => { window.open("https://mbganesh.github.io/my_profile/") }} style={{color:Helper().primaryColor , fontWeight:'bold' , cursor:'pointer'}}> @mbganesh </span> </p>
      </div>

    </div>
  );
}
